import React from 'react';
import * as BABYLON from 'babylonjs';
import 'babylonjs-loaders';
import { BabylonScene, SceneEventArgs } from './BabylonScene';

export const BViewer: React.FC = () => {
  const onSceneMount = (e: SceneEventArgs) => {
    const { canvas, scene, engine } = e;

    // This creates and positions a free camera (non-mesh)
    //let camera = new BABYLON.FreeCamera("camera1", new BABYLON.Vector3(0, 0, -1.4), scene);
    let camera = new BABYLON.ArcRotateCamera("camera1", -Math.PI/2, Math.PI/2, 1.7, BABYLON.Vector3.Zero(), scene);

    //camera.setTarget(BABYLON.Vector3.Zero());  // This targets the camera to scene origin
    camera.attachControl(canvas, true);  // This attaches the camera to the canvas

    // This creates a light, aiming 0,1,0 - to the sky (non-mesh)
    let light = new BABYLON.HemisphericLight("light1", new BABYLON.Vector3(0, 2, -1), scene);
    //let light = new BABYLON.PointLight("Omni", new BABYLON.Vector3(1,1,1), scene);

    // Default intensity is 1. Let's dim the light a small amount
    light.intensity = 0.9;
    light.diffuse = new BABYLON.Color3(1, 1, 1);
    //Light direction is directly down from a position one unit up, slow decay
    /*
    let light2 = new BABYLON.SpotLight("light2", new BABYLON.Vector3(-.5, 1, -.5), new BABYLON.Vector3(0, -1, 0), Math.PI / 2, 10, scene);
    light2.diffuse = new BABYLON.Color3(1, 0, 0);
    light2.specular = new BABYLON.Color3(0, 1, 0);
     */

    // Move the light with the camera
    // scene.registerBeforeRender(function () {
    //  light.position = camera.position;
    // });

    scene.clearColor = new BABYLON.Color4(0,0,0, 0);  // transparent background, white color will make an outline

    let mat = new BABYLON.StandardMaterial("eye", scene);
    mat.diffuseTexture = new BABYLON.Texture("http://i.imgur.com/5AbIaih.png", scene);
    mat.diffuseTexture.hasAlpha = true;
    mat.backFaceCulling = false;
    mat.emissiveColor = new BABYLON.Color3(1, 0, 1);

    // Our built-in 'sphere' shape. Params: name, subdivs, size, scene
    //let sphere = BABYLON.Mesh.CreateSphere("sphere1", 16, 2, scene);
    let sphere = BABYLON.MeshBuilder.CreateSphere("sphere1", {diameter:.15}, scene);
    sphere.material = mat;
    sphere.position = new BABYLON.Vector3(.32, -.325, 0);  // Move the sphere upward 1/2 its height
    //sphere.rotation.y = -Math.PI/2;
    scene.registerBeforeRender(function() {  // animation
      sphere.rotation.y -= .005;
    });

    let groundMat = new BABYLON.StandardMaterial("groundMat", scene);
    groundMat.diffuseColor = new BABYLON.Color3(0/255, 128/255, 128/255);
    //groundMat.wireframe = true;

    let lensMat = new BABYLON.StandardMaterial("lensMat", scene);
    lensMat.diffuseColor = new BABYLON.Color3(1, 0, 1);
    lensMat.alpha = .5;
    let frameMat = new BABYLON.StandardMaterial("frameMat", scene);
    frameMat.diffuseColor = new BABYLON.Color3(0, 0, 0);
    let armLMat = new BABYLON.StandardMaterial("armLMat", scene);
    armLMat.emissiveColor = new BABYLON.Color3(.5, 0, 0);
    armLMat.wireframe = true;
    let armRMat = new BABYLON.StandardMaterial("armLMat", scene);
    armRMat.emissiveColor = new BABYLON.Color3(0, 0, 1);
    armRMat.wireframe = true;

    let lensMat2 = new BABYLON.StandardMaterial("lensMat2", scene);
    lensMat2.diffuseColor = new BABYLON.Color3(0, 0, 0);
    lensMat2.alpha = .5;
    let frameMat2 = new BABYLON.StandardMaterial("frameMat2", scene);
    frameMat2.diffuseColor = new BABYLON.Color3(0, 0, 0);
    
    // Our built-in 'ground' shape. Params: name, width, depth, subdivs, scene
    // let ground = BABYLON.Mesh.CreateGround("ground1", 6, 6, 2, scene);
    let ground = BABYLON.MeshBuilder.CreateGround("ground1", {height: 1, width: 1, subdivisions: 10}, scene);
    ground.material = groundMat;
    ground.position.y = -.4;

    const MODEL_PATH = "./models/";

    // Available Meshes: https://doc.babylonjs.com/resources/meshes_to_load
    // Append glTF model to scene.
    //BABYLON.SceneLoader.Append("https://www.babylonjs.com/Assets/DamagedHelmet/glTF/", "DamagedHelmet.gltf",
    //BABYLON.SceneLoader.Append("./", "BoseAlto.obj", scene, function (scene) {
    // In this case we're loading our mesh from an external source.
    let wireMat = new BABYLON.StandardMaterial("wireMat", scene);
    wireMat.emissiveColor = new BABYLON.Color3(0/255, 255/255, 255/255);
    wireMat.wireframe = true;
    wireMat.alpha = .1;

    BABYLON.SceneLoader.ImportMesh("", MODEL_PATH, "face.obj", scene, function (meshes) {
      const sc = .0052;
      meshes.forEach(m => {
        // console.log(m.name);
        m.material = wireMat;
        m.rotation.y = Math.PI;
        m.scaling = new BABYLON.Vector3(sc, sc, sc);
        m.position = new BABYLON.Vector3(0, .13, .1);
        scene.registerBeforeRender(function() {  // animation
          m.rotation.y -= .002;
        })
      });
    });

    BABYLON.SceneLoader.ImportMesh("", MODEL_PATH, "BoseAlto.obj", scene, function (meshes) {
      const sc = 5;
      meshes.forEach(m => {
        //console.log(m.name);
        m.rotation.y = Math.PI;
        m.rotation.x = -Math.PI/8;
        m.scaling = new BABYLON.Vector3(sc, sc, sc);
        m.position = new BABYLON.Vector3(0, .22, .1);
        if (m.name === "hdrSkyBox") {
          m.dispose();
        }
        switch (m.name) {
          case "_mm1":              m.material = lensMat;   break;
          case "Alto_Frames":       m.material = frameMat;  break;
          case "Alto_Frame_Arm_L":  m.material = armLMat;   break;
          case "Alto_Frame_Arm_R":  m.material = armRMat;   break;
          default:
        }
        scene.registerBeforeRender(function() {  // animation
          m.rotation.y -= .002;
        })
      });
    });

    BABYLON.SceneLoader.ImportMesh("", MODEL_PATH, "BoseAlto.obj", scene, function (meshes) {
      const sc = 1;
      meshes.forEach(m => {
        m.material = (m.name === "_mm1") ? lensMat2 : frameMat2;
        m.rotation.y = Math.PI;
        m.scaling = new BABYLON.Vector3(sc, sc, sc);
        m.position = new BABYLON.Vector3(0,-.095,-.097);
      });
    });
    BABYLON.SceneLoader.ImportMesh("", "https://models.babylonjs.com/Lee-Perry-Smith-Head/", "head.babylon", scene, function (meshes) {
      meshes[0].position.y = -.4;
    });

    BABYLON.SceneLoader.ImportMesh("", "https://models.babylonjs.com/Skull/", "skull.babylon", scene, function (meshes) {
      const sc = .0036;
      let m = meshes[0];
      m.scaling = new BABYLON.Vector3(sc, sc, sc);
      m.position = new BABYLON.Vector3(.32, -.14, -.05);
    });

    BABYLON.SceneLoader.ImportMesh("", MODEL_PATH, "BoseAlto.obj", scene, function (meshes) {
      const sc = 1;
      meshes.forEach(m => {
        m.material = (m.name === "_mm1") ? lensMat2 : frameMat2;
        m.rotation.x = Math.PI/10;
        m.rotation.y = Math.PI;
        m.scaling = new BABYLON.Vector3(sc, sc, sc);
        m.position = new BABYLON.Vector3(.32,-.1,-.086);
        //m.rotate(BABYLON.Axis.X, Math.PI/10, BABYLON.Space.LOCAL);
      });
    });

    BABYLON.SceneLoader.ImportMesh("", "https://models.babylonjs.com/", "seagulf.glb", scene, function (meshes) {
      const sc = .0004;
      meshes[0].scaling = new BABYLON.Vector3(sc, sc, sc);
      meshes[0].position = new BABYLON.Vector3(-.3, -.194, 0.25);
    });

    BABYLON.SceneLoader.ImportMesh("", MODEL_PATH, "BoseRondo.obj", scene, function (meshes) {
      const sc = 1;
      meshes.forEach(m => {
        m.material = (m.name === "_mm1") ? lensMat2 : frameMat2;
        m.rotation.y = Math.PI;
        m.scaling = new BABYLON.Vector3(sc, sc, sc);
        m.position = new BABYLON.Vector3(-.3,-.11,-.016);
        m.rotate(BABYLON.Axis.X, -Math.PI/2+Math.PI/10, BABYLON.Space.LOCAL);
      });
    });

    BABYLON.SceneLoader.ImportMesh("", MODEL_PATH, "BoseRondo.obj", scene, function (meshes) {
      const sc = 1;
      meshes.forEach(m => {
        //console.log(m.name);
        m.rotation.y = Math.PI*1.2;
        m.rotation.x = -Math.PI/20;
        m.material = (m.name === "_mm1") ? lensMat2 : frameMat2;
        m.scaling = new BABYLON.Vector3(sc, sc, sc);
        m.position = new BABYLON.Vector3(0,-.375,-.22);
        if (m.name === "Rondo_Frame_Arm_L") {
          m.rotation.y += Math.PI/4;
          m.position.x -= .02;
          m.position.z -= .062;
        }
      });
    });

    engine.runRenderLoop(() => {
      if (scene) {
        scene.render();
      }
    });
  }

  return (
    <div>
      <BabylonScene onSceneMount={onSceneMount} engineOptions={{alpha: true, antialias: true}} width={1000} height={1000} />
    </div>
  )
}
